/*------------------------------------*    $RESET
\*------------------------------------*/
/* Border-Box http:/paulirish.com/2012/box-sizing-border-box-ftw/ */
*, *:after, *:before {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

html, body, div, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, ol, ul, li, form, legend, label, table, header, footer, nav, section, figure {
  margin: 0;
  padding: 0; }

header, footer, nav, section, article, hgroup, figure {
  display: block; }

/*------------------------------------*    $GLOBAL ELEMENTS
\*------------------------------------*/
/*------------------------------------*    $GLOBAL CLASSES
\*------------------------------------*/
/* Clearfix */
.cf {
  *zoom: 1; }

.cf:before, .cf:after {
  content: " ";
  /* 1 */
  display: table;
  /* 2 */ }

.cf:after {
  clear: both; }

/* Completely remove from the flow and screen readers. */
.is-hidden {
  display: none !important;
  visibility: hidden !important; }

/* Completely remove from the flow but leave available to screen readers. */
.is-vishidden {
  position: absolute !important;
  overflow: hidden;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px); }

/* Floats */
.right {
  float: right;
  padding: 0 0 1rem 1rem; }

.right-search {
  float: right;
  padding: 0 0 1rem 0; }

.left {
  float: left;
  padding: 0 1rem 1rem 0; }

/* Text-Align */
.alignright {
  text-align: right; }

.aligncenter {
  text-align: center; }

.alignleft {
  text-align: left; }

/* Display Classes */
@media all and (max-width: 46.8em) {
  .hide-small {
    display: none; } }

@media all and (min-width: 46.8em) and (max-width: 50em) {
  .hide-med {
    display: none; } }

@media all and (min-width: 50em) {
  .hide-large {
    display: none; } }

.valid {
  color: #089e00; }

.error {
  color: #f00; }

/* Text */
.cursive {
  font-style: italic; }

/* Background Colours */
.bg--white {
  background-color: #fff; }

.bg--black {
  background-color: #252525; }

.bg--red {
  background-color: #000; }

div.pos--init {
  position: initial; }

.pos--rel {
  position: relative; }

.pos--abs {
  position: absolute; }

/* margins */
.m-t-xxl {
  margin-top: 30px; }

.m-t-xxxl {
  margin-top: 40px; }

.m-b-xxl {
  margin-bottom: 30px; }

.m-b-xxxl {
  margin-bottom: 40px; }

/* Special Grey Box */
.special-box {
  background-color: #f8f3f3;
  border: 1px solid #d5d2d5; }
  .special-box:after, .special-box:before {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    background-color: #fff;
    height: 1px;
    width: 100%; }
  .special-box:before {
    top: 0; }
  .special-box:after {
    bottom: 0; }

html, body {
  font: 100%/1.5 "HelveticaNeue", "Helvetica", "Arial", sans-serif; }

body {
  margin: 0;
  overflow-x: hidden; }

.wrap {
  padding-bottom: 40px; }

img {
  max-width: 100%; }

a {
  color: #D16C5C;
  text-decoration: none; }
  a:hover {
    color: #D16C5C;
    text-decoration: underline; }

iframe {
  max-width: 100%; }

ul {
  list-style-position: inside; }

.content {
  margin: 0 0 0 280px !important;
  max-width: 100% !important;
  width: auto !important; }

h1, h2, h3 {
  font-family: "Voltaire", cursive;
  font-weight: normal;
  color: #D16C5C; }

h1 {
  font-size: 2.5rem; }

h2 {
  font-size: 2.5rem; }

h3 {
  font-size: 28px;
  margin: 0;
  margin-bottom: 10px; }

h4 {
  font-family: "HelveticaNeue", "Helvetica", "Arial", sans-serif;
  margin: 6px 0 3px;
  font-weight: normal; }

h5 {
  font-family: "HelveticaNeue", "Helvetica", "Arial", sans-serif;
  font-size: 18px;
  color: #999;
  font-weight: normal; }

.content p {
  margin: 1rem 0;
  font-size: 20px; }

input,
textarea {
  height: 32px;
  margin: 0px 0 15px;
  width: 100%;
  padding: 4px 6px;
  border: 1px solid rgba(20, 75, 106, 0.25);
  font-size: 14px;
  font-family: Arial, sans-serif; }

textarea {
  height: 79px;
  display: inherit; }

input[type="submit"] {
  color: #c2615f;
  text-transform: uppercase;
  cursor: pointer;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  background: white;
  border-color: #c2615f; }
  input[type="submit"]:hover {
    color: #fff;
    background-color: #D16C5C;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease; }

/*------------------------------------*    $LAYOUT
\*------------------------------------*/
.row {
  width: 100%;
  margin: 0 auto;
  max-width: 87rem;
  *zoom: 1; }
  .row:before, .row:after {
    content: " ";
    display: table; }

.row:after {
  clear: both; }

.row.collapse > .column,
.row.collapse > .columns {
  position: relative;
  padding-left: 0;
  padding-right: 0;
  float: left; }

.row.collapse .row {
  margin-left: 0;
  margin-right: 0; }

.row .row {
  width: auto;
  max-width: none;
  *zoom: 1; }

.row .row:before, .row .row:after {
  content: " ";
  display: table; }

.row .row:after {
  clear: both; }

.row .row.collapse {
  width: auto;
  margin: 0;
  max-width: none;
  *zoom: 1; }

.row .row.collapse:before, .row .row.collapse:after {
  content: " ";
  display: table; }

.row .row.collapse:after {
  clear: both; }

