/*------------------------------------*    $PAGE STRUCTURE
\*------------------------------------*/
.banner {
  left: 0;
  right: -280px;
  width: 280px;
  min-height: 100%;
  min-height: 100vh;
  margin: 0;
  border: none;
  border-radius: 0;
  background-color: #252525;
  position: fixed; }

.brand {
  width: 100%;
  text-align: center;
  display: inline-block;
  padding: 60px 20px 20px;
  font-size: 50px;
  text-transform: uppercase;
  font-family: "Voltaire", cursive; }
  .brand:hover {
    text-decoration: none; }

.elastic-wrapper {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  padding-top: 25px;
  height: 0; }
  .elastic-wrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

.widget {
  margin-bottom: 25px; }

.widget_categories ul {
  text-align: center;
  list-style: none; }

.widget_categories li {
  text-decoration: none;
  border: 1px solid #D5D2D5;
  padding: 6px 12px;
  display: inline-block;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  margin: 10px 4px 0;
  font-size: 14px; }
  .widget_categories li a {
    color: #43837B; }
  .widget_categories li:hover {
    background-color: #43837B;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease; }
    .widget_categories li:hover a {
      color: #fff;
      text-decoration: none; }

.widget_archive select {
  height: 40px;
  width: 100%;
  border: 1px solid #D5D2D5;
  padding-left: 10px;
  font-family: Arial, sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  color: #43837B;
  background: none; }

.widget_text {
  text-align: center; }
  @media all and (min-width: 46.8em) {
    .widget_text {
      text-align: left; }
      .widget_text img {
        width: 100%; } }

.s2_form_widget label,
.s2_form_widget br {
  display: none; }

.content-info {
  position: absolute;
  bottom: 10px;
  left: 0;
  right: 0;
  text-align: center; }

